const addSmoothScroll = () => {
  document.querySelectorAll('a[href^="#"]').forEach(anchor => {
    anchor.addEventListener('click', event => {
      event.preventDefault();

      document.querySelector(event.currentTarget.getAttribute('href')).scrollIntoView({
        behavior: 'smooth',
        block: 'start',
      });
    });
  });
};

const addMenuShadowHanlder = () => {
  const menuElem = document.getElementById('menu');

  document.addEventListener('scroll', () => {
    if (window.pageYOffset > 0) {
      menuElem.classList.add('menu_shadow');
    } else {
      menuElem.classList.remove('menu_shadow');
    }
  });
};

const insertAge = () => {
  const birthday = Date.UTC(1998, 5, 08);
  const today = Date.now();

  const ageInSeconds = today - birthday;

  const ageInYears = Math.floor(ageInSeconds / (60 * 60 * 24 * 365.25 * 1000));

  const ageElem = document.getElementById('age');
  ageElem.innerText = ageInYears;
};

const onDocumentLoad = () => {
  addSmoothScroll();
  addMenuShadowHanlder();
  insertAge();
};

document.addEventListener('DOMContentLoaded', onDocumentLoad);
